import React, { useState } from "react";
import { Container, Nav, Navbar } from "react-bootstrap";
import "./Navibar.css";
import { Link } from "react-router-dom";
import ModalComponent from "../../Common/ModalComponent/ModalComponent";
import file from "../../Assets/Anthony Bernard Cereno.pdf";
import { useEcommerceContext } from "../EcommerceContext/EcommerceContext";

const Navibar = () => {
  const { cart } = useEcommerceContext();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const [showModal, setShowModal] = useState(false);
  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => setShowModal(true);
  const handleToggle = () => {
    setShow((prevShow) => !prevShow);
  };

  return (
    <div className="navibar text-light">
      <Navbar
        collapseOnSelect
        expand="lg"
        className="p-2 position-fixed bg-dark"
      >
        <Container>
          <Navbar.Toggle
            onClick={handleToggle}
            aria-controls="responsive-navbar-nav"
          />
          <Navbar.Collapse in={show} id="responsive-navbar-nav">
            <Nav>
              <Link
                onClick={handleClose}
                className="me-3 text-decoration-none text-light"
                to={"/"}
              >
                Home
              </Link>
              <a
                className="me-3 text-decoration-none text-light"
                href={file}
                download
              >
                Download CV
              </a>
              <Link
                onClick={handleShowModal}
                className="me-3 text-decoration-none text-light"
              >
                View CV
              </Link>
            </Nav>
            <Nav>
              <Link onClick={handleClose}
                to="/cart" class="btn btn-primary position-relative">
                Cart{" "}
                <span class="position-absolute top-0 start-100 translate-middle badge border border-light rounded-circle bg-danger p-2">
                  <span class="visually-hidden">{cart.length}</span>
                </span>
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
        <ModalComponent show={showModal} handleClose={handleCloseModal} />
      </Navbar>
    </div>
  );
};

export default Navibar;
